<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field
      v-model="name"
      :rules="nameRules"
      :counter="10"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>
    <v-text-field
      v-model="phoneHome"
      :rules="phoneRules"
      label="Phone Number - Home"
      required
    ></v-text-field>
    <v-text-field
      v-model="phoneMobile"
      :rules="phoneRules"
      label="Phone Number - Mobile"
      required
    ></v-text-field>
    <v-text-field
      v-model="address"
      :rules="addressRules"
      label="Address Line"
      required="true"
    ></v-text-field>
    <!-- <v-select
      v-model="select"
      :items="items"
      :rules="[v => !!v || 'Country is required']"
      label="Country"
    ></v-select> -->
    <v-checkbox
      v-model="checkbox"
      :rules="[v => !!v || 'You must agree to continue!']"
      label="Do you agree?"
      required
    ></v-checkbox>

    <v-btn
      :disabled="!valid"
      @click="submit"
    >
      submit
    </v-btn>
    <v-btn @click="clear">clear</v-btn>
  </v-form>
</template>
<script>
  import axios from 'axios'

  export default {
    name: 'BookerForm',
    data: () => ({
        valid: true,
        name: '',
        nameRules: [
          v => !!v || 'Name is required',
          v => (v && v.length <= 10) || 'Name must be less than 10 characters'
        ],
        email: '',
        phoneMobile: '',
        phoneHome: '',
        address: '',
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid'
        ],
        select: null,
        items: [
          'Country 1',
          'Country 2',
          'Country 3',
          'Country 4'
        ],
        checkbox: false
      }),

      methods: {
        submit () {
          if (this.$refs.form.validate()) {
            // Native form submission is not yet supported
            axios.post('/api/submit', {
              name: this.name,
              email: this.email,
              // select: this.select,
              phoneMobile: this.phoneMobile,
              phoneHome: this.phoneHome,
              address: this.address,
              checkbox: this.checkbox
            })
          }
        },
        clear () {
          this.$refs.form.reset()
        }
      }
  }
</script>
